@import url('https://fonts.googleapis.com/css2?family=Onest:wght@300&display=swap');


input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

@media screen and (min-width: 1920px) {
    .logoWhite {
        aspect-ratio: auto;
        width: auto;
        height: 50px;
    }
    #dot16 {
        position: absolute;
        height: 250px;
        width: 250px;
        background-color: #617077;
        border-radius: 50%;
        top: 23%;
        right: 30%;
        z-index: 0;
    }

    #dot17 {
        position: absolute;
        height: 300px;
        width: 300px;
        background-color: #253339;
        border-radius: 50%;
        top: 57%;
        right: 15%;
        z-index: 0;
    }

    #dot18 {
        position: absolute;
        height: 100px;
        width: 100px;
        background-color: #101E25;
        border-radius: 50%;
        top: 17%;
        right: 90%;
        z-index: 0;
    }

    #dot19 {
        position: absolute;
        height: 400px;
        width: 400px;
        background-color: #0f1d24;
        border-radius: 50%;
        top: 70%;
        right: 60%;
        z-index: 0;
    }

    #dot20 {
        position: absolute;
        height: 200px;
        width: 200px;
        background-color: #1c3d4d;
        border-radius: 50%;
        top: 90%;
        right: 1%;
        z-index: 0;
    }

    .backroundHoversL {
        animation: VibrateL 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversM {
        animation: VibrateM 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversH {
        animation: VibrateH 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversU {
        animation: VibrateU 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }
}
@media screen and (min-width: 2560px) {
    .logoWhite {
        aspect-ratio: auto;
        width: auto;
        height: 60px;
    }
    #dot16 {
        position: absolute;
        height: 500px;
        width: 500px;
        background-color: #617077;
        border-radius: 50%;
        top: 23%;
        right: 30%;
        z-index: 0;
    }

    #dot17 {
        position: absolute;
        height: 400px;
        width: 400px;
        background-color: #253339;
        border-radius: 50%;
        top: 57%;
        right: 15%;
        z-index: 0;
    }

    #dot18 {
        position: absolute;
        height: 200px;
        width: 200px;
        background-color: #101E25;
        border-radius: 50%;
        top: 17%;
        right: 90%;
        z-index: 0;
    }

    #dot19 {
        position: absolute;
        height: 600px;
        width: 600px;
        background-color: #0f1d24;
        border-radius: 50%;
        top: 70%;
        right: 60%;
        z-index: 0;
    }

    #dot20 {
        position: absolute;
        height: 200px;
        width: 200px;
        background-color: #1c3d4d;
        border-radius: 50%;
        top: 90%;
        right: 1%;
        z-index: 0;
    }

    .backroundHoversL {
        animation: VibrateL 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversM {
        animation: VibrateM 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversH {
        animation: VibrateH 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversU {
        animation: VibrateU 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }
}

@keyframes VibrateL {
    0% {
        transform: translate(0)
    }

    20% {
        transform: translate(-2px, 2px)
    }

    40% {
        transform: translate(-2px, -2px)
    }

    60% {
        transform: translate(2px, 2px)
    }

    80% {
        transform: translate(2px, -2px)
    }

    to {
        transform: translate(0)
    }
}

@keyframes VibrateM {
    0% {
        transform: translate(0)
    }

    20% {
        transform: translate(-4px, 4px)
    }

    40% {
        transform: translate(-4px, -4px)
    }

    60% {
        transform: translate(4px, 4px)
    }

    80% {
        transform: translate(4px, 4px)
    }

    to {
        transform: translate(0)
    }
}

@keyframes VibrateH {
    0% {
        transform: translate(0)
    }

    20% {
        transform: translate(-7px, 3px)
    }

    40% {
        transform: translate(-3px, 1px)
    }

    60% {
        transform: translate(1px, -2px)
    }

    80% {
        transform: translate(7px, 4px)
    }

    to {
        transform: translate(0)
    }
}

@keyframes VibrateU {
    0% {
        transform: translate(0)
    }

    15% {
        transform: translate(-15px, 7px)
    }

    30% {
        transform: translate(-7px, 3px)
    }

    45% {
        transform: translate(-2px, 0px)
    }

    60% {
        transform: translate(2px, -1px)
    }

    75% {
        transform: translate(7px, -4px)
    }

    90% {
        transform: translate(13px, 7px)
    }

    to {
        transform: translate(0)
    }
}