.download {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 10em;
}

.demo {
    aspect-ratio: auto;
    width: 1000px;
    z-index: 1;
}

.headerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: space-evenly;
    margin-top: 5em;
    margin-bottom: 5em;
    width: 100%;
}

.biggerheaderContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.carousal {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: space-around;
    width: 100%;
    margin-bottom: 5em;
}

.lowerThird {
    display: flex;
    flex-direction: row;
    background-color: #2E3F4C;
    height: 100%;
    z-index: 1;
}

.dividercontainer {
    display: flex;
    flex-direction: row;
}

@media screen and (min-width: 1920px) {
    #dot1 {
        position: absolute;
        height: 350px;
        width: 350px;
        background-color: #4B636D;
        border-radius: 50%;
        top: 10%;
        right: 75%;
        z-index: 0;
    }

    #dot2 {
        position: absolute;
        height: 300px;
        width: 300px;
        background-color: #253339;
        border-radius: 50%;
        top: 50%;
        right: 40%;
        z-index: 0;
    }

    #dot3 {
        position: absolute;
        height: 100px;
        width: 100px;
        background-color: #101E25;
        border-radius: 50%;
        top: 18%;
        right: 5%;
        z-index: 0;
    }

    #dot4 {
        position: absolute;
        height: 400px;
        width: 400px;
        background-color: #617077;
        border-radius: 50%;
        top: 85%;
        right: 65%;
        z-index: 0;
    }

    #dot5 {
        position: absolute;
        height: 250px;
        width: 250px;
        background-color: #1c3d4d;
        border-radius: 50%;
        top: 90%;
        right: 1%;
        z-index: 0;
    }

    .backroundHoversL {
        animation: VibrateL 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversM {
        animation: VibrateM 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversH {
        animation: VibrateH 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .backroundHoversU {
        animation: VibrateU 2s ease 0s infinite normal forwards;
        opacity: 1 !important;
        visibility: visible !important;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }
}

@media screen and (min-width: 2560px) {
    #dot1 {
        position: absolute;
        height: 350px;
